.current-events {
  &__title {
    margin-bottom: 40px;

    @include section-title-text;

    text-align: center;

    color: $c-main-black;

    @include onTablet {
      margin-bottom: 48px;

      text-align: left;
    }

    @include onDesktop {
      margin-bottom: 56px;
    }
  }

  &__event {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDesktop {
      margin-bottom: 104px;
    }
  }
}
